.inavii-social-feed-app {
  &__select-account {
    $border: 1px dashed #d9d9d9;

    border: $border;

    .ant-collapse-item {
      border-bottom: $border;

      &:last-of-type {
        border-bottom: none;
      }
    }

    .ant-collapse-content {
      border-top: $border;
    }

    .ant-table-wrapper {
      overflow-x: auto;

      table {
        width: 100%;
        min-width: 600px;
        overflow-x: auto;

        tr {
          &:last-of-type > td {
            border-bottom: none;
          }
        }

        td {
          font-size: 13px;
          padding: 8px;

          &:nth-child(1),
          &:nth-child(2) {
            min-width: 200px;
          }

          &:nth-child(3),
          &:nth-child(4) {
            min-width: 50px;
          }
        }
      }
    }

    .ant-tag {
      display: inline-flex;
      align-items: center;
      padding: 4px 8px;
      cursor: pointer;

      &:hover {
        opacity: .6;
      }

      .anticon {
        font-size: 13px;
      }
    }
  }
}