.inavii-social-feed-app {
  &__options-feed {
    margin-top: 24px;
    border-radius: 8px;
    border: 1px dashed #d9d9d9;
    position: sticky;
    top: 40px;
    max-height: calc(100vh - 160px);
    overflow: auto;

    .ant-collapse-header-text {
      font-weight: 700;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    .ant-card {
      border: none;
      box-shadow: none;

      &:hover {
        .ant-card-cover {
          .anticon,
          &::before {
            opacity: 1;
          }
        }

        .ant-card-meta-description {
          text-decoration: underline;
        }
      }
    }

    .ant-collapse-header {
      align-items: center;
    }

    .ant-card-cover {
      position: relative;
      border-radius: 4px;
      overflow: hidden;
      isolation: isolate;

      .anticon {
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, -50%);
        color: #ffffff;
        font-size: 60px;
        opacity: 0;
        transition: opacity .3s ease-in-out;
        z-index: 2;
      }

      &::before {
        content: '';
        position: absolute;
        inset: 0;
        opacity: 0;
        background-color: rgba(0, 0, 0, .5);
        transition: opacity .3s ease-in-out;
        z-index: 1;
      }
    }

    .ant-card-meta-description {
      color: #000000e0;
      line-height: 1.4;
    }

    .ant-card-body {
      padding: 0;
    }

    .ant-card-meta {
      flex-direction: column;
      align-items: center;
      margin: 0;
      gap: 10px;
    }

    .ant-collapse-item {
      border-bottom: 1px dashed #d9d9d9;
    }

    .ant-radio-group,
    .ant-checkbox-group {
      display: flex;
      flex-direction: column;
    }

    .ant-select-selector {
      height: auto;
    }

    .inavii__pro {
      opacity: .30;
      filter: grayscale(50%);
      pointer-events: none;
      margin-right: 0;

      > span {
        padding-right: 0;
      }
    }

    .inavii-social-feed-app__label-pro {
      .ant-scroll-number-custom-component {
        right: 0;
        top: 0;
        height: 0;
        width: 0;
        border-radius: 4px;
        color: #ffffff;
        background-color: #7a3cff;
        font-weight: bold;
        font-size: 0;
        padding: 4px;
        min-width: auto;
        transform: translate(100%, -50%);
        transition: all .15s ease;
        line-height: 1;
        cursor: pointer;
        overflow: hidden;

        &:hover {
          padding: 2px 6px;
          height: auto;
          width: auto;
          font-size: 10px;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }

    .ant-radio-wrapper .ant-radio-input {
      opacity: 0;
    }
  }

  &__options-feed-subtitle {
    display: flex;
    align-items: center;
    padding-bottom: 8px;

    img {
      height: 30px;
      width: 30px;
      margin-left: 10px;
    }
  }

  &__options-feed-item {
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__upgrade-pro {
    width: 100%;
    margin-bottom: 8px;
    padding: 4px 6px;
    display: flex;
    align-items: center;
    background-color: #FFFFFF;
    color: #000000;
    white-space: normal;
    border: 1px dashed #7a3cff;

    &:hover {
      background-color: #9c66ff;
      color: #ffffff;
      text-decoration: underline;
    }

    .anticon {
      font-size: 14px;
    }
  }
}