.inavii-social-feed-app {
  &__steps-container {
    max-width: 1024px;
    margin-left: auto;
    margin-right: auto;
  }

  &__steps {
    max-width: 1104px;
    margin-left: auto;
    margin-right: auto;

    .ant-steps-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 100%;
    }

    .ant-steps-item-title {
      white-space: normal;
    }

    &-buttons-box {
      .gutter-row {
        &:first-of-type {
          text-align: left;
        }

        &:last-of-type {
          text-align: right;
        }
      }
    }
  }
}