.inavii-social-feed-app {
  &__modal-promotion-post-container {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    max-height: 100vh;
    width: 100%;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    z-index: 999;

    .ant-modal-mask {
      position: fixed;
      background-color: rgba(#000, 0.3);
    }

    .ant-modal-wrap {
      position: absolute;
      display: flex;
      top: 0;
      height: inherit;
    }
  }

  &__modal-promotion-post {
    top: 0;
    max-width: 600px;
    max-height: 100vh;
    background-color: #ffffff;
    border-radius: 8px;
    margin: auto;
    padding-bottom: 0;

    .ant-modal-body {
      padding: 0;
    }

    .ant-modal-content {
      padding: 30px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: unset;
      background-color: transparent;
      box-shadow: none;
    }

    .ant-modal-close {
      pointer-events: auto !important;
      background-color: #7a3cff;
      opacity: 1;

      svg {
        height: 70%;
        width: 70%;
        margin: 0 auto;

        path {
          fill: #ffffff !important;
        }
      }

      .ant-modal-close-x {
        display: flex;
        align-items: center;
        justify-content: center;
      }

      &:hover {
        svg {
          path {
            fill: #7a3cff !important;
          }
        }
      }
    }
  }
}