.inavii-social-feed-app {
  &__layouts {
    &-cards {
      padding: 15px;
      height: 100%;

      &:hover,
      &.active {
        border-color: #7a3cff;
        box-shadow: 0 1px 2px -2px rgb(122 60 255 / 16%), 0 3px 6px 0 rgb(122 60 255 / 12%), 0 5px 12px 4px rgb(122 60 255 / 9%)
      }

      &-back {
        opacity: 0;
        visibility: hidden;
        transition: all .2s;
        pointer-events: none;
      }

      &.disable {
        overflow: hidden;
        background-color: rgba(#000, .08);

        .ant-card-cover {
          &::before {
            content: 'PRO';
            position: absolute;
            top: 7px;
            right: -70px;
            height: 21px;
            width: 170px;
            font-size: 11px;
            font-weight: 700;
            color: #FFF;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #7A3CFF;
            transform: rotate(45deg);
            z-index: 1;
            pointer-events: none;
          }

          .ant-image {
            filter: grayscale(.8);
          }
        }

        .inavii-social-feed-app__layouts-cards-back {
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          gap: 8px;
          backdrop-filter: blur(10px);
        }

        &:hover {
          .inavii-social-feed-app__layouts-cards-back {
            opacity: 1;
            visibility: visible;
            pointer-events: all;
          }
        }
      }

      .ant-card-cover {
        height: 180px;

        .ant-image {
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
        }

        img {
          max-width: max-content;
          max-height: 100%;
          border-radius: 0;
        }
      }

      .ant-card-body {
        padding: 15px 0 0;
      }
    }
  }
}