.inavii-social-feed-app {
  &__account-info-box {
    width: 320px;
    color: rgba(0, 0, 0, 0.6);

    .ant-card-body {
      padding-top: 8px;
      text-align: left;
    }
  }

  &__account-info-button-box {
    display: flex;
    flex-direction: column;
    justify-content: center;

    button {
      &:first-of-type {
        margin-top: 0;
      }
    }
  }

  &__account-info-bio-button-box {
    display: flex;
    justify-content: flex-end;
    gap: 5px;
    padding-top: 5px;
  }

  &__account-info-avatar {
    display: flex;
    align-items: center;
    gap: 20px;

    &-img {
      height: 70px;
      width: 70px;
      min-width: 70px;
      background-color: transparent;
      border: none;
    }
  }

  &__account-info-button {
    width: max-content;
  }

  &__account-info-button-pro {
    padding: 0;
    line-height: 1.5;
    font-size: 14px;

    .inavii__pro {
      font-weight: bold;
      color: #000000;
    }
  }

  &__account-info-bio-area {
    color: #000000;
    background-color: #efefef;
    border-radius: 6px;
    padding: 2px 6px;
  }

  &__account-info-button--delete,
  &__account-info-bio-edit {
    margin-top: 16px;
    color: #7a3cff;
    font-weight: 400;
    cursor: pointer;
    transition: color .3s;

    &:hover,
    &:focus {
      color: #ac8dfc;
    }
  }

  &__account-info-title {
    font-weight: bold;
    margin-right: 6px;
    display: inline-flex;
    color: #000000;
  }

  &__account-info-type {
    text-transform: uppercase;

    span {
      text-transform: capitalize;
    }
  }

  &__account-info-bio-edit {
    display: flex;
    margin-top: 0;
  }

  &__account-info-avatar,
  &__account-info-username,
  &__account-info-nickname,
  &__account-info-user-id,
  &__account-info-type {
    padding-bottom: 10px;
  }
}