.inavii-social-feed-app {
  $inavii-global-class: '.inavii-social-feed-app__';

  &__grid {
    svg {
      width: 16px;
    }
  }

  &__media-type {
    position: absolute;
    top: 8px;
    right: 8px;
    width: 20px !important;
    z-index: 1;
    pointer-events: none;
  }

  &__feed-name {
    text-align: left;
  }

  &__feeds-container:not(#{$inavii-global-class}feeds-container-slider) {
    #{$inavii-global-class}grid {
      min-width: 800px;
    }
  }

  &__grid {
    &:not(.inavii-social-feed-app__masonry-vertical):not(.inavii-social-feed-app__masonry-horizontal):not(.inavii-social-feed-app__slider) {
      display: grid;
      gap: 6px;
      grid-auto-flow: dense;
      line-height: initial;
    }

    &-item {
      position: relative;
      border-radius: 8px;
      overflow: hidden;
      cursor: pointer;
      align-self: flex-start;

      &::before {
        content: " ";
        display: block;
        padding-top: 100%;
        width: 100%;
      }

      &-box {
        bottom: 0;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;

        .ant-image-mask-info {
          display: none;
        }

        .ant-image,
        img {
          display: flex;
          height: 100%;
          object-fit: cover;
          transition: transform .2s;
        }
      }

      &:hover {
        img {
          transform: scale(1.1);
        }

        .inavii-social-feed-app__box-description {
          background-color: rgba(0, 0, 0, .8);
          opacity: 1;
          transform: scaleX(1);
        }
      }
    }

    &#{$inavii-global-class}gallery,
    &#{$inavii-global-class}row {
      gap: 0 !important;

      #{$inavii-global-class}grid-item {
        border-radius: 0;
      }
    }

    &#{$inavii-global-class}wave {
      padding-top: 24px;
    }
  }

  &__skeleton.ant-skeleton-image {
    position: absolute;
    top: 0;
    left: 0;
    height: 100% !important;
    width: 100% !important;

    .ant-skeleton-image-svg {
      display: none;
    }
  }

  &__box-description {
    align-items: flex-start;
    display: none;
    flex-direction: column;
    height: calc(100% - 20px);
    font-family: 'Cabin', sans-serif;
    justify-content: space-around;
    padding: 10px;
    width: calc(100% - 20px);
    left: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    transform: scale(1.5);
    transition: all .3s;
    color: #FFFFFF;
    z-index: 2;
    pointer-events: none;

    p {
      margin: 0;
    }
  }

  &__demo {
    .inavii-social-feed-app__box-description {
      display: flex;
    }
  }

  &__top-box {
    display: block;
    overflow: auto;
    text-align: center;
    width: 100%;
  }

  &__avatar-box {
    align-items: center;
    display: flex;
    justify-content: flex-start;
    padding-bottom: 5px;
  }

  &__avatar-content {
    align-items: center;
    border: 1px solid #7a3cff;
    border-radius: 50%;
    display: flex;
    height: 32px;
    justify-content: center;
    margin-right: 10px;
    min-width: 32px;
    overflow: hidden;
    position: relative;
    width: 32px;
  }

  &__nickname,
  &__description {
    -webkit-box-orient: vertical;
    display: block;
    display: -webkit-box;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__nickname {
    word-wrap: break-word;
    -webkit-line-clamp: 1;
    font-size: 18px;
    font-weight: 600;
    text-align: left;
  }

  &__middle-box {
    width: 100%;
  }

  &__description {
    -webkit-line-clamp: 3;
    font-size: 13px;
    text-align: center;
  }

  &__icons-box {
    padding: 5px 0;
    width: 100%;

    svg {
      margin-right: 4px;
    }
  }

  &__icons-box,
  &__icons-box-right {
    align-items: center;
    display: flex;
    justify-content: center;
    width: 100%;
  }

  &__icon-text {
    font-size: 12px;
  }

  &__icons-box p {
    align-items: center;
    display: flex;
    justify-content: space-between;

    &:first-of-type {
      margin-right: 10px;
    }
  }

  &__grid:not(#{$inavii-global-class}slider) {
    grid-template-columns: repeat(5, 1fr);
  }

  &__highlight {
    grid-template-columns: repeat(6, 1fr) !important;
  }

  &__highlight-focus,
  &__highlight-super {
    grid-template-columns: repeat(4, 1fr) !important;
  }

  &__masonry-horizontal {
    column-count: 6;
    column-gap: 6px;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    row-gap: 6px;

    .inavii-social-feed-app__grid-item,
    .inavii-social-feed-app__feed-item {
      height: 150px;
    }
  }

  &__masonry-vertical {
    grid-template-columns: unset;
    column-gap: 6px;
    column-count: 5;

    .inavii-social-feed-app__grid-item {
      align-items: center;
      display: inline-flex;
      margin-bottom: 6px;
      width: 100%;

      &--height-0 {
        height: 160px;
      }

      &--height-1 {
        height: 175px;
      }

      &--height-2 {
        height: 190px;
      }

      &--height-3 {
        height: 205px;
      }

      &--height-4 {
        height: 220px;
      }

      &--height-5 {
        height: 235px;
      }

      &--height-6 {
        height: 250px;
      }

      &--height-7 {
        height: 260px;
      }

      &--height-8 {
        height: 270px;
      }

      &--height-9 {
        height: 280px;
      }

      &--height-10 {
        height: 290px;
      }
    }
  }

  &__slider {
    min-height: 260px;

    .inavii-social-feed-app__grid {
      &-item {
        height: 100%;

        &::before {
          display: none;
        }

        &-box {
          position: relative;
          display: inline-block;
          width: 100%;
          height: 100%;
        }
      }
    }

    .slick-list {
      .slick-slide {
        height: 220px;
        display: flex;

        > div:not(.inavii-social-feed-app__grid-item) {
          display: flex;
          column-gap: 6px;
          width: 100%;
        }
      }
    }

    .inavii-social-feed-app__slider-container {
      > .slick-arrow {
        &.slick-next {
          //inset-inline-end: 0 !important;
        }
      }
    }

    .slick-dots {
      position: relative;
      margin: 0;
      bottom: -20px;

      li.slick-active,
      li {
        button {
          background: #7a3cff;
        }
      }
    }

    .slick-arrow {
      opacity: 1 !important;

      &:not(.slick-disabled) {
        &:hover {
          opacity: .8 !important;
        }
      }

      svg > path {
        fill: #7a3cff !important;
      }

      &.slick-disabled {
        display: none !important;
        filter: grayscale(1);
        cursor: unset;
      }

      &.slick-next {
        inset-inline-end: -40px !important;
      }

      &.slick-prev {
        inset-inline-start: -40px !important;
      }
    }
  }

  &__edit-feed {
    &#{$inavii-global-class}slider {
      display: grid;
      width: 100%;
      grid-auto-flow: column;
      grid-auto-rows: max-content;
      box-sizing: border-box;
      min-width: 350px;
      grid-gap: 8px;
      overflow: auto;

      #{$inavii-global-class}feed-item {
        height: 220px;
        width: 220px;
      }
    }
  }
}