.inavii-social-feed-app {
  $self: &;

  &__feed-image {
    height: 100% !important;
    width: 100%;
    object-fit: cover;
  }

  #{$self}__masonry-vertical {
    #{$self}__feed-item {
      align-items: center;
      display: inline-flex;
      margin-bottom: 6px;
      width: 100%;
    }
  }

  &__grid {
    &:not(#{$self}__gallery):not(#{$self}__row) {
      #{$self}__feed-item {
        border-radius: 8px;
      }
    }

    &#{$self}__wave {
      #{$self}__feed-item {
        height: fit-content;
      }
    }
  }

  &__feed-item {
    aspect-ratio: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transform-origin: 0 0;

    &:focus,
    &:hover,
    #{$self}__feed-item-active {
      #{$self}__feed-center-box {
        opacity: 1;
      }
    }

    &.inavii-pointer {
      cursor: pointer;
    }

    &.locked {
      cursor: not-allowed;
    }
  }

  &__feed-item-box {
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;

    .ant-image {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
    }
  }

  &__feed-open-modal,
  &__feed-is-locked {
    position: absolute;
    left: 5px;
    cursor: pointer;
    z-index: 2;
    translate: 0;
  }

  &__feed-open-modal {
    top: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    border-radius: 50%;
    height: 30px;
    width: 30px;

    &:hover {
      background: #9c66ff !important;
    }

    svg {
      width: 16px;
      height: 16px;
      object-fit: contain;
    }
  }

  &__feed-is-locked {
    bottom: 5px;
  }

  &__feed-is-locked,
  &__feed-show-hidden {
    img {
      width: 20px;
      height: 20px;
      object-fit: contain;
    }

    &.ant-switch.ant-switch-checked .ant-switch-inner {
      padding-inline-end: 14px;
    }

    .ant-switch-inner {
      display: flex;
      align-items: center;
      padding-inline-start: 14px;
    }

    .ant-switch-inner-checked {
      display: inline-block !important;
      margin-top: 0 !important;
    }

    .ant-switch-inner-unchecked {
      display: inline-block !important;
      margin-top: 0 !important;
    }
  }

  &__feed-center-box {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 56px;
    width: 56px;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: 1px solid #7a3cff;
    background-color: #fff;
    z-index: 2;
    cursor: grab;
    opacity: 0;
    visibility: visible;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    svg {
      width: 40px;
      height: 40px;
      object-fit: contain;
    }

    &.locked {
      cursor: not-allowed;

      svg {
        margin-top: 1px;
        margin-left: 1px;
        width: 40px;
        height: 40px;
      }
    }
  }

  &__feed-show-hidden {
    position: absolute;
    right: 5px;
    bottom: 5px;
    z-index: 2;
    translate: 0;
  }

  &__feed-item-hidden {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      backdrop-filter: blur(5px);
      background-color: rgba(#fff, 0.5);
      z-index: 1;
    }
  }

  &__feeds-container:not(#{$self}__feeds-container-slider) {
    overflow: auto;
  }
}