.inavii-social-feed-app {
  &__done {
    padding-left: 0;
    padding-right: 0;

    .anticon {
      color: #7a3cff !important;
    }

    &-video {
      width: 100%;
      border-radius: 8px;
    }

    &-container {
      .ant-image {
        width: 100%;
        border: 1px dashed rgb(217, 217, 217);
        border-radius: 8px;
        overflow: hidden;
      }
    }
  }
}