.inavii-social-feed-app {
  &__modal-container {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    z-index: 999;

    .ant-modal-wrap {
      position: absolute;
      top: 0;
      display: flex;
    }

    .ant-modal-mask {
      position: fixed;
      background-color: rgba(#000, 0.7);
    }

    .ant-modal,
    .ant-modal-content {
      position: unset;
    }

    .ant-modal-close {
      @keyframes fade-in {
        0% {
          opacity: 0;
        }
        100% {
          opacity: 1;
        }
      }
      opacity: 0;
      animation: fade-in 1s .5s forwards;

      .ant-modal-close-x {
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }

  .slick-track {
    display: flex;
    align-items: center;
  }

  .slick-arrow {
    pointer-events: auto !important;
    cursor: pointer;
    border-radius: 8px;
    height: 40px;
    width: 40px;
    opacity: .8;
    display: flex !important;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    transform: translateY(-50%);

    &.slick-prev {
      inset-inline-start: -45px;
    }

    &.slick-next {
      inset-inline-end: -45px;
    }

    svg {
      height: 100%;
      width: 100%;

      path {
        fill: #ffffff;
      }
    }
  }

  &__modal-carousel {
    top: 0;
    max-width: 600px;
    max-height: 100vh;
    width: calc(100% - 80px) !important;
    height: 100%;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 0;

    * {
      pointer-events: none !important;
    }

    &-inner {
      .slick-prev,
      .slick-next {
        background-color: #ffffff;
        border-radius: 50% !important;
        height: 25px !important;
        width: 25px !important;
        opacity: .7;

        svg {
          height: 70%;
          width: 70%;

          path {
            fill: rgba(0, 0, 0, .7) !important;
          }
        }

        &:hover,
        &:focus {
          background-color: #ffffff !important;
        }
      }

      .slick-prev {
        inset-inline-start: 10px !important;
      }

      .slick-next {
        inset-inline-end: 10px !important;
      }
    }

    img,
    video {
      height: 100%;
      width: 100%;
      max-height: 80vh;
      object-fit: contain;
      pointer-events: auto !important;
      max-width: 100%;
      border-radius: 8px;
      user-select: none;
      background-color: #000000;
    }

    .slick-slide {
      display: flex !important;
      justify-content: center;
      align-items: center;

      &:not(.slick-active) {
        img,
        video {
          pointer-events: none !important;
          display: none !important;
        }
      }

      > div {
        width: 100%;

        > div:not(.inavii-social-feed-app__modal-carousel-slider) {
          display: flex !important;
          justify-content: center;
        }
      }
    }

    .ant-modal-body,
    .slick-slide {
      max-height: 80vh;
      padding: 0;
    }

    .slick-slide {
      max-height: 100%;
    }

    .ant-modal-content {
      padding: 0;
    }

    .ant-modal-content {
      background-color: transparent;
      box-shadow: none;
    }

    .slick-prev,
    .slick-next {
      z-index: 1;

      &.slick-disabled {
        display: none !important;
      }
    }

    .ant-modal-close {
      pointer-events: auto !important;
      background-color: #ffffff;

      svg {
        height: 70%;
        width: 70%;
        margin: 0 auto;

        path {
          fill: rgba(0, 0, 0, .7) !important;
        }
      }
    }

    .ant-carousel {
      height: 80vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    .slick-list {
      height: auto !important;
    }

    .slick-slide{
      height: 100% !important;
    }

    .ant-image {
      display: flex;
      align-items: center;
      width: 100%;
    }
  }

  &__video-box {
    position: relative;
    cursor: pointer;
    width: 100%;
  }

  &__play-video {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: .7;
    width: 120px;
    height: 120px;
    pointer-events: none;
  }
}