.inavii-social-feed-app {
  &__accounts-container {
    .ant-table-wrapper {
      overflow-x: auto;

      table {
        width: 100%;
        min-width: 600px;
        overflow-x: auto;

        td {
          font-size: 13px;
          padding: 8px;

          &:nth-child(1),
          &:nth-child(2) {
            min-width: 200px;
          }

          &:nth-child(3),
          &:nth-child(4) {
            min-width: 150px;
          }
        }
      }
    }

    .ant-tag {
      display: inline-flex;
      align-items: center;
      padding: 4px 8px;
      cursor: pointer;

      &:hover {
        opacity: .6;
      }
    }
  }

  &__accounts-username {
    display: flex;
    align-items: center;

    &-name {
      color: #7a3cff;
      cursor: pointer;
      transition: color .3s;

      &:hover,
      &:focus {
        color: #ac8dfc;
      }
    }

    .ant-avatar {
      display: flex;
      justify-content: center;
      margin-right: 6px;
      height: 34px;
      min-width: 34px;
      background-color: transparent;
      border: none;

      img {
        height: 34px;
        width: 34px;
      }
    }
  }

  &__account-button {
    display: flex;
    align-items: center;
    text-decoration: none;

    .anticon {
      margin-right: 6px;
      font-size: 26px;
    }
  }

  &__accounts-type {
    color: rgba(0, 0, 0, 0.45);
  }

  &__tag-button {
    font-size: 12px;
  }
}

.ant-popover-inner {
  max-width: 280px;
}