.inavii-social-feed-app {
  $inavii-social-feed-app: '.inavii-social-feed-app';

  &__wrapper {
    position: relative;
    padding: 40px;
    border-radius: 8px;
    border: 1px dashed #d9d9d9;
    margin-top: 24px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;

    &:not(#{$inavii-social-feed-app}__full-width) {
      max-width: 1024px;
    }
  }

  &__title-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px;
    border: 1px dashed #d9d9d9;
    border-radius: 8px;
    margin-bottom: 40px;
  }

  &__title {
    font-size: 32px;
  }

  &__onboarding {
    #{$inavii-social-feed-app}__title {
      color: #000000;
      font-size: 20px;
    }

    &#{$inavii-social-feed-app}__wrapper {
      padding-bottom: 0;
    }
  }

  &__single-feed {
    &#{$inavii-social-feed-app}__wrapper {
      padding-bottom: 0;
    }
  }

  &__steps-buttons-box,
  &__single-feed #{$inavii-social-feed-app}__buttons-navigation,
  &__onboarding #{$inavii-social-feed-app}__buttons-navigation {
    position: sticky;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    background-color: #ffffff;
    margin-top: 16px;
    padding-top: 16px;
    padding-bottom: 16px;
    z-index: 2;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -40px;
      width: calc(100% + 80px);
      border-top: 1px dashed #d9d9d9;
    }
  }

  &__done-container ~ #{$inavii-social-feed-app}__buttons-navigation {
    justify-content: flex-end;
  }
}