.inavii-social-feed-app {
  &__account {
    text-align: left;

    &:not(:last-of-type) {
      margin-bottom: 5px;
    }

    &:last-of-type {
      margin-bottom: 24px;
    }

    &-box {
      height: 100%;

      .ant-card-body {
        height: 100%;
      }
    }

    p {
      margin: 0;
    }

    &-notification {
      padding-left: 10px;

      .ant-badge-status-dot {
        margin-left: -12px;
      }

      .ant-badge-status-text {
        margin-inline-start: 6px !important;
      }
    }

    &-link {
      display: flex;
      align-items: flex-start;
      white-space: normal;
      height: auto !important;

      span {
        font-size: 14px;
      }

      .anticon {
        padding-top: 2px;

        svg {
          height: 17px;
          width: 17px;

          path {
            fill: #7a3cff;
          }
        }
      }
    }
  }

  &__link {
    &:hover,
    &:focus {
      color: #ac8dfc!important;
    }
  }
}