.inavii-social-feed-app {
  &__single-feed-container {
    max-width: 1440px;
    margin-left: auto !important;
    margin-right: auto !important;
  }

  &__single-feed {
    max-width: 100%;
  }

  &__notifications-global-settings {
    &.disable {
      position: relative;
      overflow: hidden;
      background-color: rgba(#000, .08);

      &::before {
        content: 'PRO';
        position: absolute;
        top: 7px;
        right: -70px;
        height: 21px;
        width: 170px;
        font-size: 11px;
        font-weight: 700;
        color: #FFF;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #7A3CFF;
        transform: rotate(45deg);
        z-index: 1;
        pointer-events: none;
      }

      a {
        pointer-events: auto;
      }

      input {
        background-color: rgba(#000, .08);
      }

      .inavii-social-feed-app__layouts-cards-back {
        position: absolute;
        top: 0;
        left: 0;
        height: calc(100% - 40px);
        width: calc(100% - 40px);
        padding: 20px;
        display: flex;
        align-items: center;
        flex-direction: column;
        gap: 8px;
        backdrop-filter: blur(10px);
      }

      &:hover {
        border-color: #7a3cff!important;

        .inavii-social-feed-app__layouts-cards-back {
          opacity: 1;
          visibility: visible;
          pointer-events: all;
        }
      }
    }
  }

  &__select-interval {
    padding: 20px 0!important;

    .ant-result-icon {
      margin-bottom: 8px;
    }

    .anticon-close-circle {
      font-size: 40px!important;
    }

    .ant-result-title {
      font-size: 16px;
      font-weight: 700;
      margin-bottom: 0;
    }
  }
}