@import url(https://fonts.googleapis.com/css2?family=Cabin:wght@400;700&display=swap);

.inavii-social-feed-app {
  padding: 0 0 30px;
  font-family: 'Cabin', sans-serif;
  background-color: #ffffff;
}

#inavii-social-feed-app {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  background-color: #fff;
  min-height: 100vh;
  height: max-content;

  .inavii-button-pro {
    background-color: #dd224b;
    display: flex;
    align-items: center;
    box-shadow: none;
    transition: all .2s ease;

    &:hover {
      text-decoration: none;
      box-shadow: inset 0 0 50px rgba(0, 0, 0, .1);
      background-color: #dd224b !important;
    }

    .ant-btn-icon {
      font-size: 18px;
    }
  }
}

.wp-admin #inavii-social-feed-app {
  margin-left: -20px;
  min-height: calc(100vh - 32px);
}

.wp-admin .inavii-social-feed-app__modal-container,
.wp-admin .inavii-social-feed-app__modal-settings-container {
  width: calc(100% - 160px);
  top: 32px;
  right: 0;
  left: auto;
  height: calc(100vh - 32px);
}

@media only screen and (max-width: 960px) {
  .wp-admin .inavii-social-feed-app__modal-container,
  .wp-admin .inavii-social-feed-app__modal-settings-container {
    width: calc(100% - 36px);
  }
}

@media only screen and (max-width: 782px) {
  .wp-admin .inavii-social-feed-app__modal-container,
  .wp-admin .inavii-social-feed-app__modal-settings-container {
    width: 100%;
    top: 46px;
    height: calc(100vh - 46px);
  }
}

body {
  margin: 0;
}