.inavii-social-feed-app {
  &__header {
    background: transparent;
    height: auto;
    line-height: normal;
    padding: 15px;
    border-bottom: 1px solid rgb(217, 217, 217);
    box-shadow: 0 0 15px rgba(0, 0, 0, .15);


    &-column-right {
      display: flex;
      justify-content: flex-end;
      flex-wrap: wrap;
      gap: 16px;
    }
  }
}